.product-detailed {
  display: flex;
  margin: 0 auto;
  margin-bottom: 100px;
  max-width: 1170.4px;
}

.product-detailed__gallery {
  margin-top: 10px;
  margin-right: 8.5%;
  overflow-x: hidden;
}

.product-detailed__gallery-list {
  display: grid;
  grid-template-columns: 1fr 4.7fr;
  column-gap: 30px;
  row-gap: 30px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.product-detailed__gallery-item {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-column: 1 / 2;

  &--big {
    grid-row: 1 / 4;
    grid-column: 2 / -1;
    margin-top: auto;
    margin-bottom: auto;
  }
}

.product-detailed__gallery-item img {
  display: block;
}

.product-detailed__info {
  margin: 0;
  flex-basis: 370px;
  max-width: 470px;
}

.product-detailed__info h2 {
  margin: 0;
  margin-bottom: 10px;
  font-family: $second-font;
  font-weight: 700;
  font-size: 34px;
  line-height: 44px;
  color: #1d1613;
}

.product-detailed__info p {
  margin: 0;
  margin-bottom: 37px;
  font-family: $main-font;
  font-weight: 400;
  font-size: 26px;
  line-height: 34px;
  color: #1d1613;
}

.product-detailed__info-link {
  display: inline-block;
  margin-right: 31px;
  vertical-align: middle;
  color: #9b9997;

  &--active {
    color: #1d1613;
  }
}

.product-detailed__description {
  margin: 0;
  margin-top: 25px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  row-gap: 16px;
  margin-bottom: 58px;
}

.product-detailed__description dt {
  grid-column: 1 / 2;
  font-size: 14px;
}

.product-detailed__description dd {
  grid-column: 2 / -1;
  margin-left: auto;
  font-size: 14px;
  font-weight: 400;
}

.product-detailed__info .product-detailed__button {
  display: block;
  text-align: center;
  margin-bottom: 13px;
  padding-top: 22px;
  padding-bottom: 21px;
}

.product-detailed__conditions-list {
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
}

.product-detailed__conditions-list li {
  margin-right: 10px;
  font-size: 14px;
  text-align: center;
  color: #9b9997;

  &:last-child {
    margin-right: 0;
  }
}

@media (max-width: $tablet-width) {
  .product-detailed {
    margin-bottom: 69px;
  }

  .product-detailed__gallery {
    margin-right: 40px;
    flex-basis: 370px;
  }

  .product-detailed__gallery-list {
    grid-template-columns: repeat(3, 1fr);
    column-gap: 14px;
    row-gap: 14px;
  }

  .product-detailed__gallery-item {
    grid-column: auto;
    grid-row: 2 / 3;
    margin: 0 auto;

    &--big {
      grid-row: auto;
      grid-column: 1 / -1;
      margin-top: auto;
      margin-bottom: auto;
    }
  }

  .product-detailed__conditions-list li {
    font-size: 12px;
    line-height: 19px;
  }
}

@media (max-width: $mobile-width) {
  .product-detailed {
    display: block;
    margin-bottom: 54px;
  }

  .product-detailed__gallery {
    margin-right: 0;
    margin-bottom: 40px;

    &--nojs .product-detailed__gallery-list {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
      margin: 0 auto;
      margin-top: 15px;
      max-width: 470px;
    }

    &--nojs .product-detailed__gallery-item {
      margin: 0;
      margin-left: 5px;
      margin-right: 5px;

      &--big {
        order: -1;
      }

      &:last-child {
        margin-left: 0;
        margin-right: 0;
      }
    }
  }

  .product-detailed__gallery-list {
    grid-template-columns: repeat(4, 1fr);
    column-gap: 0;
    align-items: center;
  }

  .product-detailed__gallery-item {
    grid-row: 1 / 2;

    &--big {
      grid-column: 1 / 2;
    }
  }

  .product-detailed__info {
    margin: 0 auto;
  }

  .product-detailed__info p {
    margin-bottom: 28px;
  }

  .product-detailed__description {
    margin-bottom: 48px;
  }

  .product-detailed__info .product-detailed__button {
    margin-bottom: 8px;
  }

  .product-detailed__conditions-list li {
    margin-right: 40px;
  }
}
